import React from "react"
import Fade from "react-reveal/Fade"
import uwaterlootaImg from "./../../images/workPhotos/uwaterlootaImg.jpg"
import SEO from "../../components/seo"
import Layout from "../../components/layout"

import '../../css/uwaterloota.scss';
import 'bootstrap/dist/css/bootstrap.min.css';


const uwaterloota = () => (
  <Layout>
    <SEO title="TA University of Waterloo" />
      <div
        className="container"
        style={{
          backgroundImage:
            "linear-gradient(to right, rgba(245, 246, 252, 0), rgba(0, 0, 0, 0.4)),url(" +
            uwaterlootaImg +
            ")",
        }}
      >
        <div className="content">
          <Fade bottom>
            <h1>Teaching Assistant University of Waterloo</h1>
            <h2>09/2020 - 12/2020</h2>
            <p>
              pt1
              <br></br>
              <br></br>
              pt2
              <br></br>
              <br></br>
              pt3
            </p>
          </Fade>
        </div>
      </div>
  </Layout>
)

export default uwaterloota
